pf-page {
  --page-drawer-panel_max-width: 400px;
}

pf-page .pf-v5-c-panel__main::part(panel) {
  overflow: var(--pf-v5-c-panel__main--Overflow);
  height: 100%;
  max-height: 100%;
}

pf-page pf-panel.notification-box {
  background-color: #fff;
  width: 350px;
  height: 100%;
  display: none;
  position: absolute;
  right: 0;
  box-shadow: 0 1px 5px #000000bf;
}

pf-page pf-panel.notification-box.active {
  width: 350px;
  height: 100%;
  display: block;
  position: absolute;
  right: 0;
  box-shadow: 0 1px 5px #000000bf;
}

pf-page .raw-infos {
  justify-content: space-between;
  align-items: center;
  gap: 10px;
  margin: 10px;
  padding: 10px;
  display: flex;
}

pf-page .raw-infos:nth-child(odd) {
  background-color: #9e9e9e59;
}

pf-page .raw-infos:nth-child(2n) {
  background-color: #9e9e9e17;
}

pf-page .down-arrow:nth-child(2n) {
  cursor: pointer;
  background-color: #fff;
  border-radius: 3px;
  justify-content: center;
  align-items: center;
  width: 24px;
  height: 24px;
  display: flex;
}
